import React from "react";
import logo from './images/images.png';
const Header = ()=>{
    return(
        <>
        <div className="header">
        <img src={logo} alt="logo" width="80" />   {/*height="80"*/} 
        <h1> @Keeps For You </h1>
        </div>
        </>
    );
};

export default Header; 